<template>
 <div>
   <div class="nav-wrap">
      <span><a href="#about">关于公司</a></span>
      <span><a href="#product">产品介绍</a></span>
     <span><a href="#fung">明星菌类</a></span>
     <span><a href="#cooperate">合作伙伴</a></span>
   </div>
   <span class="banner-wrap">
      <img class="img"  src="../assets/1.jpg" alt="">
   </span>
   <span class="banner-wrap" id="about">
      <img class="img"  src="../assets/2.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/3.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/4.jpg" alt="">
   </span>
   <span class="banner-wrap" id="product">
      <img class="img"  src="../assets/5.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/6.jpg" alt="">
   </span>
   <span class="banner-wrap" id="fung">
      <img class="img"  src="../assets/7.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/8.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/9.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/10.jpg" alt="">
   </span>
   <span class="banner-wrap">
      <img class="img"  src="../assets/11.jpg" alt="">
   </span>
   <span class="banner-wrap" id="cooperate">
      <img class="img"  src="../assets/12.jpg" alt="">
   </span>
<!--   <span class="banner-wrap">-->
<!--      <img class="img"  src="../assets/13.jpg" alt="">-->
<!--   </span>-->
   <span class="top"  v-show="scrollTop1>200" @click="backTop"><img src="../assets/top.jpg" alt=""></span>
 </div>
</template>

<script>
  export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      top:document.documentElement.scrollTop,
      scrollTop1:0
    }
  },
    mounted () {
      window.addEventListener('scroll', this.scrollToTop)
    },
  methods:{
    backTop() {
        setTimeout(()=>{
          document.documentElement.scrollTop = 0;
        },10)
        // setInterval(()=>{
        //   if(document.documentElement.scrollTop ){
        //     return
        //   }
        //   document.documentElement.scrollTop=document.documentElement.scrollTop -50
        // },5)
    },
    scrollToTop() {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.scrollTop1 = scrollTop
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .img{
    width:100%;
  }
  .nav-wrap{
    widt:100%;
    display:flex;
    justify-content: center;
    align-items:center;
    height:100px;
    line-height:100px;
    font-size:20px;
    background:#ededed;

  }
  .nav-wrap>span{
    width:25%;
  }
  .nav-wrap>span:hover{
      background:red;
      color:#ccc;
      transition: 0.5s;
    cursor:pointer;

  }

  @media screen and(max-width:750px) {
    .nav-wrap{
      font-size:14px;
      height:75px;
      line-height:75px;
    }
  }
  .top{
    width: 40px;
    height:40px;
    display: block;
    position:fixed;
    right:20px;
    bottom:40px;
    cursor:pointer;
  }
  .top>img{
    width:40px;
    height:40px;

  }
</style>



